import React from 'react' 
import "../styles/Fonts.css"
import '../styles/TopScreenBottom.css'
export default function TopScreenBottom() {
    return (
        <div id='top_screen_bottom'>
            <div className='container' style={{maxWidth:'1200px'}}>
                <div className="row">
                    <div className='col-md-4 info_count blue'>
                        <span id="countup" data-count="5">5</span>лет
                        <div className='info_desc_bottom_screen'>Радуем стабильной работой<br/>
                    и постоянными<br/>
                    обновлениями</div>
                    </div>
                    <div className='col-md-4'>
                    <div id="info_count_green_figure">
						<span className="countup2" data-count="211488" style={{fontSize:'90px'}}>211 488</span><br/>пользователей
                        <p className="info_desc_bottom_screen">
						Оценили сервис и<br/>постоянно используют его в<br/>своём бизнесе
					</p>
                    </div>
                    </div>
                    <div className='col-md-4 info_count blue'>
                    </div>
                </div>
            </div>
        </div>
    )
}
