import React from 'react'
import logo from '../Media/Images/logo.png'
import './Header.css'
import '../styles/Fonts.css'
export default function Navbar() {
    return (
        <header className='header'
        style={{width:'100%',
            height:'80px',
            backgroundColor:'rgba(255, 255, 255, 0.95)',
            zIndex:'10',
            boxShadow:'0 0 3px #000',
            position:'fixed'}}>
            <div className="container" style={{maxWidth:'1200px'}}>
            <nav className='navbar navbar-default navbar-expand-md' style={{paddingLeft:'15px',paddingRight:'15px'}}>
                <div className='d-flex align-items-center'>
                    <div className='d-inline-block navbar-brand'>
                        <img src={logo} alt="logo" className='w-100 logo' />
                    </div>
                    <p className='relative logo_desc' style={{textAlign:'left',fontSize:'11px',lineHeight:'.92',display:'inline-block',color:'#777',width:'90px',height:'33px',margin:0}}>Программа для <br/> лидогенерации <br/>в соцсетях</p>
                </div>
                <div className='navbar-collapse collapse'>
                    <ul className='navbar-nav mr-auto'>
                        <li className='nav-item'><p className='nav-link scroll' href='#'>Что это?</p></li>
                        <li className='nav-item'><p className='nav-link scroll'>Тарифы</p></li>
                        <li className='nav-item'><p className='nav-link scroll'>Возможности</p></li>
                        <li className='nav-item'><p className='nav-link scroll'>Кейсы</p></li>
                        <li className='nav-item'><p className='nav-link scroll'>Услуги</p></li>
                        <li className='nav-item'><p className='nav-link scroll'>База знаний</p></li>
                        <li className='nav-item'><p className='nav-link scroll'>Новости</p></li>
                    </ul>
                    <ul className='navbar-nav nav-btns'>
                        <li className='nav-item pb-0 mb-0'>
                            <p className='nav-link-btn btn-green load_soft'>Скачать</p></li>
                        <li className='nav-link scroll text-center p-0' style={{maxWidth:'133px',display:'inline-flex'}}>
                            <p className='nav-link-btn btn-login' href='https://tvigi.com/personal-area'>Профиль</p>
                            </li>
                    </ul>
                </div>
            </nav>
        </div>
        </header>
    )
}
