import React from 'react'
import telegram from '../Media/Images/telegram.png'
import vk from '../Media/Images/vk.png'
import instagram from '../Media/Images/instagram.png'
import flying_robot from '../Media/Images/flying_robot.png'
import '../styles/TopSlide.css'
export default function TopSlide() {
    return (
        <div className='top_screen'>
            <div className='container' style={{maxWidth:'1200px'}}>
                <div className="row">
                    <div className="col-md-6 left-content">
                        <div className="top_screen_btns">
                            <p className='mb-0' style={{paddingLeft:'20px',paddingRight:'15px',color:'rgb(102,102,102)',fontSize:'14px'}}>Лиды от 1 рубля из</p>
                            <div className='img_block'>
                                <img src={telegram} alt="" />
                                <img src={vk} alt="" />
                                <img src={instagram} alt="" />
                            </div>
                        </div>
                        <h2>
                            Программа №1 <br/>для дешевой <br/> лидогенерации в соц. сетях
                        </h2>
                        <a href="https://www.youtube.com/watch?v=1K1dJi9RZpo?autoplay=1" data-fancybox-play="video-more" className="more">Подробнее</a>
                        <p className='btn-green-big load_soft'>Попробовать бесплатно</p>
                    </div>
                    <div className="right_content col-md-6">
                        <img className="slide_fly_robot fly" id="flying_robot" src={flying_robot} alt="flying_robot"/>
                    </div>
                </div>
            </div>
        </div>
    )
}
